import focudate  from  '@/components/focudate.vue'
import focutime  from  '@/components/focutime.vue'


export default {
	components:{
		focudate,
		focutime,
	},
	
	data() {
		return {
			lesEval: [],
			firsttime:'',
			lasttime:'',
			state:{
				focudate:false,
				focutime:false
			},
			idx:'0',
			Stime:'',
			etime:''
		};
	},
	onLoad() {
		var time = Date.parse(new Date())
		this.lasttime= this.getLocalTime(time)
		this.firsttime = this.getLocalTime(time-7*24*60*60*1000)
	},
	onShow(){
		this.getinfo();
	},
	methods: {
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/assessmentliessonlist',
				data: this.getmd5({
					startTime:this.firsttime,
					endTime:this.lasttime,
					openId:uni.getStorageSync('openId'),
					// openId:'onR7C05SKrVWwNSZrlQgPbMEL4t4'
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						this.lesEval = [];
						var data = res.data.data;
						this.lesEval = data;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
		goTo(id){
			uni.navigateTo({
				url:'../evalDetail/evalDetail?id='+id
			})
		},
		ltime(e){
			if(this.Stime == 'firsttime'){
				if(e>this.lasttime){
					uni.showToast({
						icon:"none",
						title:'日期选择错误',
						duration:2000
					})
				}else{
					this.firsttime = e
					this.closePop()
				}	
			}else {
				if(e<this.firsttime){
					uni.showToast({
						icon:"none",
						title:'日期选择错误',
						duration:2000
					})
				}else{
					this.lasttime = e
					this.closePop()
				}
			}
		},
		closePop(){
			this.state.focutime = false;
		},
		focutimes(type){
			this.Stime = type ;
			if(type == 'firsttime') {
				this.etime = this.firsttime
			}else {
				this.etime = this.lasttime
			}
			this.state.focutime=true;
		},
		firsttimes(e){
			this.firsttime = e
		},
		lasttimes(e){	
			this.lasttime = e
		},
		idXX(e){
			this.idx = e
			this.state.focudate = false; 
		},
		getLocalTime(nS) {     
		    return new Date(nS).toJSON().substr(0, 19).replace('T', ' ').replace(/-/g, '-').slice(0,10);
		} 
		
	},
}
